import React from 'react';
import { Card, CardImg, CardBody, CardTitle, Progress } from 'reactstrap';
import { PokemonPropTypes } from '../../lib/PokemonPropTypeValues';
import './Pokemon.css';

const defaultProps = {
  defeated: false,
  battle: false,
  onClick(event) {
    event.preventDefault();
  },
};

function Pokemon(props) {
  return (
    <Card onClick={props.onClick} className={props.defeated ? 'defeated' : ''}>
      <CardImg top width="100%" src={`${process.env.PUBLIC_URL}/${props.img}`} alt={props.name} />
      <CardBody>
        <CardTitle>{props.name}</CardTitle>

        {props.battle && (
          <div>
            <p>{props.level}</p>
            <div>{props.currentHp} / {props.totalHp}</div>
            <Progress value={(props.currentHp / props.totalHp) * 100} />
          </div>
        )}
      </CardBody>
    </Card>
  );
}

Pokemon.propTypes = PokemonPropTypes.isRequired;
Pokemon.defaultProps = defaultProps;

export default Pokemon;



