import React from 'react';
import PropTypes from 'prop-types';
import { CardGroup, Button } from 'reactstrap';
import { PokemonPropTypes } from './../lib/PokemonPropTypeValues';
import Pokemon from './Pokemon/Pokemon';

const propTypes = {
  disabled: PropTypes.bool.isRequired,
  starter: PokemonPropTypes.isRequired,
  challenger: PokemonPropTypes.isRequired,
  attackHandler: PropTypes.func.isRequired,
};

function Arena({
  disabled, starter, challenger, attackHandler,
}) {
  return (
    <div className="arena">
      <CardGroup>
        <Pokemon {...starter} battle />
        <Pokemon {...challenger} battle />
      </CardGroup>
      <Button disabled={disabled} onClick={attackHandler}>Attack &gt;</Button>
    </div>
  );
}

Arena.propTypes = propTypes;

export default Arena;



