import React from 'react';
import ReactAudioPlayer from 'react-audio-player';
import PropTypes from 'prop-types';

const propTypes = {
  pid: PropTypes.number.isRequired,
};

const cries = {
  0: '',
  1: 'bulbasaur-cry.mp3',
  4: 'charmander-cry.mp3',
  7: 'squirtle-cry.mp3',
  25: 'pikachu-cry.mp3',
};

function SFXPlayer({ pid }) {
  return (
    <ReactAudioPlayer src={`${process.env.PUBLIC_URL}/assets/sounds/${cries[pid]}`} autoPlay />
  );
}

SFXPlayer.propTypes = propTypes;

export default SFXPlayer;



