import React from 'react';
import ReactAudioPlayer from 'react-audio-player';
import PropTypes from 'prop-types';

const propTypes = {
  state: PropTypes.string.isRequired,
};

const tracklist = {
  starterselect: '101-opening.mp3',
  challengerselect: '127-trainer-appears-boy-chapter-.mp3',
  battleround: '115-battle-vs-trainer-.mp3',
  roundresult: '115-battle-vs-trainer-.mp3',
  battleresult: '116-victory-vs-trainer-.mp3',
  end: '145-ending.mp3',
};

function MusicPlayer({ state }) {
  return (
    <ReactAudioPlayer
      src={`${process.env.PUBLIC_URL}/assets/sounds/${tracklist[state]}`}
      muted
      volume={0}
      autoPlay
      controls
      loop
    />
  );
}

MusicPlayer.propTypes = propTypes;

export default MusicPlayer;



