import React from 'react';
import PropTypes from 'prop-types';
import { CardGroup } from 'reactstrap';
import Pokemon from '../Pokemon';
import { PokemonPropTypes } from '../../lib/PokemonPropTypeValues';
import './Holster.css';


const propTypes = {
  pokemon: PropTypes.arrayOf(PokemonPropTypes).isRequired,
  clickHandler: PropTypes.func.isRequired,
};

function Holster({ pokemon, clickHandler }) {
  return (
    <div className="holster">
      <CardGroup>
        {pokemon.map(p => (
          <Pokemon
            key={p.id}
            {...p}
            onClick={() => clickHandler(p)}
          />
        ))}
      </CardGroup>
    </div>
  );
}

Holster.propTypes = propTypes;

export default Holster;



